import React from 'react';
import './home.css'
class Home extends React.Component{
    render(){
        return (
        <div className="doc">
            <h1 className="title">Le jeu de la vie</h1>
            <p>Faire crire le scnario de la vie par un ordinateur, est-ce bien srieux ? En tout cas, cela dmarre comme un jeu ! Et l'on finit par un hallucinant zoo dautomates, danges, de mortels, derrants</p> 
            <h3>Introduction</h3>
            <p>Faire crire le scnario de la vie par un ordinateur, est-ce bien srieux ? En tout cas, a dmarre comme un jeu ! Et a finit par un hallucinant zoo dautomates, danges, de mortels, derrants,     Le mathmaticien amricain dorigine hongroise John Von Neumann est un vrai gnie dont les travaux ont, entre autres, jet les bases de linformatique.<br/> Il se pose beaucoup de questions dans les annes 1950, notamment sur les automates autoreproducteurs (pouvant reproduire une exacte copie deux-mmes)    Un autre mathmaticien, Stanislas Ulam (polonais), lui suggre la mthode dcisive.<br/> Il veut crer sur des ordinateurs des jeux capables dinventer des formes gomtriques.<br/>Lunivers de Von Neumann commence ainsi par un damier o les cases, baptises cellules, peuvent revtir 2 tats : teint/allum.<br/>Cest John Conway, sur une ide de Von Neumann, linventeur du clbre jeu de la vie (1970) : jeu  un joueur dont l'objectif est la survie et la croissance d'une population reprsente par des jetons sur un quadrillage dont les cases sont des cellules tant au sens biologique que topographique...</p>
            <h3>Les rgles du jeu</h3>
            <p>Le monde du  Jeu de la vie  est un plan infini quadrill, dont chaque case est, soit occupe par une cellule, soit vide.<br/>Chaque case possde huit voisines, places tout autour delle.<br/>Dune gnration  lautre, des naissances et des dcs sy droulent mcaniquement selon la rgle simpliste de Conway :Pour un automate 2D  2 tats avec voisinage de 8, il y a 2^2^9 = 1.34^154 fonctions de transitions diffrentes possibles ! Pour expliciter chacune de ces fonctions, il faudrait dcrire l'tat de sortie correspondant  chacune des 512 configurations de voisinage.<br/>La fonction de transition du Jeu de la Vie se rsume en fait  des mta-rgles qui regroupent  elles-seules les 512 configurations.</p>
            <h5>Principe de naissance :</h5>
            <p>Si une case est vide et que trois de ses voisines sont occupes, alors une naissance sy produit (trange it!)</p>
            <h5>Principe de stase :</h5>
            <p>Si une case est occupe, la survie ny est possible que si deux ou trois cases voisines sont occupes</p>
            <h5>Principe de mort :</h5>
            <p>Si une case est entoure de 0 ou 1 voisine occupe, la case est vide  la gnration suivante (mort par isolement).<br/>Si une case est entoure de 3 voisines occupes et plus, la case est vide  la gnration suivante (mort par surpopulation).</p>
        </div>
        )
    }
}
export default Home ;


